'use client'

import { useState, useEffect } from 'react';
import { useParams } from 'next/navigation';
import { useChat, type Message as VercelChatMessage } from 'ai/react';
import { ChatMessage, Emotion } from '@/lib/types';
import ChatInterface from '@/components/chatinterface';
import { saveConversation, getConversation } from '@/lib/conversation-history';
import { Switch } from '@/components/ui/switch';
import { Label } from '@/components/ui/label';

export default function ChatPage() {
  const params = useParams<{ sessionId: string }>();
  const [initialMessages, setInitialMessages] = useState<VercelChatMessage[] | undefined>(undefined);

  useEffect(() => {
    if (!params.sessionId) return;
    
    const conversation = getConversation(params.sessionId);
    if (conversation && Array.isArray(conversation.messages)) {
      const formattedMessages: VercelChatMessage[] = conversation.messages.map(m => ({
        id: m.id,
        role: m.role,
        content: m.content,
      }));
      setInitialMessages(formattedMessages);
    } else {
      setInitialMessages([]);
    }
  }, [params.sessionId]);

  if (initialMessages === undefined || !params.sessionId) {
      return (
        <div className="flex justify-center items-center h-screen bg-sakura-bg">
          <div className="text-sakura-dark">Loading conversation...</div>
        </div>
      );
  }

  return <ChatComponent sessionId={params.sessionId} initialMessages={initialMessages} />;
}

// We create a new component to ensure useChat is not called until initialMessages is ready.
function ChatComponent({ sessionId, initialMessages }: { sessionId: string, initialMessages: VercelChatMessage[] }) {
  const [currentEmotion, setCurrentEmotion] = useState<Emotion>('Normal');
  const [isShortMode, setIsShortMode] = useState(false);
  const [chatError, setChatError] = useState<string | null>(null);

  const { messages, input, handleInputChange, handleSubmit, isLoading, error, setMessages } = useChat({
    api: '/api/chat',
    id: sessionId,
    headers: {
      'X-Session-Id': sessionId,
      'X-Short-Mode': isShortMode ? 'true' : 'false',
    },
    initialMessages: initialMessages,
    onResponse: (response) => {
      setChatError(null); // Clear error on new response
      if (response.headers.has('X-Emotion')) {
        setCurrentEmotion(response.headers.get('X-Emotion') as Emotion);
      }
    },
    onFinish: (message) => {
      // The useChat hook's `messages` state might not be updated yet.
      // We manually construct the full list to ensure we save the latest state.
      const updatedMessages = getConversation(sessionId)?.messages ?? initialMessages;
      const finalMessages = [...updatedMessages, message];

      const conversationTitle = finalMessages.find(m => m.role === 'user')?.content.substring(0, 30) || 'New Chat';
      saveConversation(sessionId, conversationTitle, finalMessages);
      
      if (finalMessages.filter(m => m.role === 'user').length === 1) {
        window.dispatchEvent(new Event('conversationHistoryChanged'));
      }
    },
    onError: (err) => {
      try {
        // Errors from the API route are now consistently structured
        const errorBody = JSON.parse(err.message);
        setChatError(errorBody.error?.message || 'An unknown error occurred. Check the server console.');
      } catch (e) {
        // Fallback for non-JSON error messages
        setChatError(err.message);
      }
      setCurrentEmotion('Sadness');
    }
  });

  const customHandleInputChange = (e: React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLTextAreaElement>) => {
    if (chatError) {
      setChatError(null);
    }
    handleInputChange(e);
  };

  const augmentedMessages: ChatMessage[] = messages.map(m => ({
    id: m.id,
    sender: m.role === 'user' ? 'user' : 'bot',
    message: m.content,
    timestamp: new Date(),
  }));

  useEffect(() => {
     if (error) {
       // Error handling logic
     }
  }, [error]);

  return (
    <div className="flex justify-center items-center h-screen bg-transparent">
       <div className="w-full h-full md:p-4 flex justify-center items-center">
            <div className="w-full h-full max-w-2xl md:h-full md:max-h-[900px] flex flex-col bg-white dark:bg-gray-800 rounded-none md:rounded-4xl shadow-lg border border-sakura-gray dark:border-gray-700">
                <div className="flex items-center justify-end p-4 border-b border-sakura-gray dark:border-gray-700">
                    <div className="flex items-center space-x-2">
                        <Switch 
                            id="short-mode"
                            checked={isShortMode}
                            onCheckedChange={setIsShortMode}
                        />
                        <Label htmlFor="short-mode" className="text-sm font-medium text-sakura-text dark:text-gray-300">
                            Short Answers
                        </Label>
                    </div>
                </div>
                <ChatInterface
                    messages={augmentedMessages}
                    input={input}
                    handleInputChange={customHandleInputChange}
                    handleSubmit={handleSubmit}
                    isLoading={isLoading}
                    currentEmotion={currentEmotion}
                    error={chatError}
                    className="h-full"
                />
            </div>
       </div>
    </div>
  );
} 